import { DocsConfig } from '@mintlify/validation';
import { GetStaticProps } from 'next';

import { getLocalPageProps, PagePropType } from '@/lib/local/page';
import { ErrorPage } from '@/ui/ErrorPage';

interface Custom404Props {
  docsConfig?: DocsConfig;
}

export const getStaticProps: GetStaticProps<Custom404Props> = async () => {
  try {
    const data: PagePropType = await getLocalPageProps('index');

    if (data.hasOwnProperty('notFound')) {
      throw new Error('Failed to fetch 404 page');
    }

    if ('docsConfig' in data) {
      return {
        props: {
          docsConfig: data.docsConfig,
        },
      };
    }

    if ('pageData' in data) {
      return {
        props: {
          docsConfig: data.pageData.docsConfig,
        },
      };
    }

    throw new Error('Failed to fetch 404 page');
  } catch (error) {
    console.error('Error fetching config for 404 page:', error);
    return { props: {} };
  }
};

export default ErrorPage;
