'use client';

import { DocsConfig } from '@mintlify/validation';
import Link from 'next/link';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import FontScript from '@/fonts/FontScript';
import { generateDynamicBackgroundDarkColor, hexToRgbString } from '@/hooks/useColors';
import { ColorVariables } from '@/ui/ColorVariables';
import { ModeToggleScript } from '@/ui/ModeToggleScript';
import { RoundedVariables } from '@/ui/RoundedVariables';

type ErrorPageProps = {
  statusCode: number;
  title?: string;
  description?: string;
  docsConfig?: DocsConfig;
};

export function ErrorPageComponent({ statusCode, title, description, docsConfig }: ErrorPageProps) {
  const colors = {
    primary: docsConfig?.colors.primary ?? '#117866',
    primaryLight: docsConfig?.colors.light ?? '#4ADE80',
    primaryDark: docsConfig?.colors.dark ?? '#166534',
    backgroundColorDark: docsConfig?.background?.color?.dark ?? '#0f1117',
    backgroundColorLight: docsConfig?.background?.color?.light ?? '#FFFFFF',
  };

  const cssVariables = `
    :root {
      --primary: ${hexToRgbString(colors.primary)};
      --primary-light: ${hexToRgbString(colors.primaryLight)};
      --primary-dark: ${hexToRgbString(colors.primaryDark)};
      --background-light: ${hexToRgbString(colors.backgroundColorLight)};
      --background-dark: ${generateDynamicBackgroundDarkColor(colors.primaryLight, colors.backgroundColorDark)};
    }
  `;

  const defaultDescription = () => {
    switch (statusCode) {
      case 404:
        return <>We&apos;re sorry, we couldn&apos;t find the page you were looking for.</>;
      case 500:
        return (
          <>
            An unexpected error occurred. Please <SupportLink /> to get help.
          </>
        );
      default:
        return 'An unexpected error occurred. Please contact support.';
    }
  };

  return (
    <DocsConfigContext.Provider
      value={{
        docsConfig,
        docsNavWithMetadata: undefined,
      }}
    >
      <ColorVariables />
      <FontScript />
      <RoundedVariables />
      <ModeToggleScript />
      <style>{cssVariables}</style>
      <main className="h-screen bg-background-light dark:bg-background-dark">
        <article className="bg-custom bg-fixed bg-center bg-cover relative flex flex-col items-center justify-center h-full">
          <div className="w-full max-w-xl px-10">
            <span className="inline-flex mb-6 rounded-full px-3 py-1 text-sm font-semibold mr-4 text-white p-1 bg-primary">
              Error {statusCode}
            </span>
            <h1 className="font-semibold mb-3 text-3xl">{title ?? 'Page not found!'}</h1>
            <p className="text-lg text-gray-600 dark:text-gray-400 mb-6">
              {description ?? defaultDescription()}
            </p>
          </div>
        </article>
      </main>
    </DocsConfigContext.Provider>
  );
}

export function SupportLink() {
  return (
    <Link
      href="mailto:support@mintlify.com"
      className="font-medium text-gray-700 dark:text-gray-100 border-b hover:border-b-[2px] border-primary-dark dark:border-primary-light"
    >
      contact support
    </Link>
  );
}
