'use client';

import { useRouter } from 'next/router';

import { ErrorPageComponent } from '@/components/errorPageComponent';
import { MintlifyRouterContext } from '@/contexts/MintlifyRouterContext';
import { ErrorPageProps } from '@/types/page';

export function ErrorPage({ docsConfig }: ErrorPageProps) {
  const router = useRouter();
  return (
    <MintlifyRouterContext.Provider value={router}>
      <ErrorPageComponent docsConfig={docsConfig} statusCode={404} />
    </MintlifyRouterContext.Provider>
  );
}
